import React from 'react';
import { Box, Link, PseudoBox, Heading } from '@chakra-ui/core';
import { FaRadiationAlt } from 'react-icons/fa';

const Navbar = () => (
  <PseudoBox p="20px" borderBottom="1px solid blue.500" borderColor="blue.300" shadow="md">
    <Link href="/">
      <Box as={FaRadiationAlt}></Box>
      <Heading fontSize="sm">Rick and Morty Wiki</Heading>
    </Link>
  </PseudoBox>
);

export default Navbar;
